import { Component, OnInit } from '@angular/core';
import { NzMessageService, NzModalService } from 'ng-zorro-antd';
import { SettingsService } from '@core/services/settings.service';
import { SidebarService } from './sidebar.service';
import { Router } from '@angular/router';

@Component({
  selector   : 'app-sidebar',
  templateUrl: './sidebar.component.html',
  providers : [SidebarService]
})
export class SidebarComponent implements OnInit {
  isVisible = false;

    constructor(public settings: SettingsService, public msgSrv: NzMessageService,
      private sidebarService: SidebarService,
      private router: Router, private _message: NzMessageService,
      private confirmServ: NzModalService) {
    }

  logout() {
    let sidebarService: SidebarService = this.sidebarService;
    let router: Router = this.router;
    this.confirmServ.confirm({
      title  : '是否确认要登出',
      onOk() {
        sidebarService.logout()
        .subscribe(data => {
          if (data.success) {
            router.navigate(['/login']);
          }else {
            console.log('登出失败');
          }
        });
      },
      onCancel() {
      }
    });
  }
    ngOnInit() {
  }
}



