import { Router } from '@angular/router';
import { Component } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { SettingsService } from '@core/services/settings.service';
import {LoginService} from './login.service';
import { StartupService } from '@core/services/startup.service';
import { NzMessageService } from 'ng-zorro-antd';

@Component({
  selector: 'app-pages-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
    providers: [LoginService]
})
export class LoginComponent {
  valForm: FormGroup;

  constructor(public settings: SettingsService, fb: FormBuilder, private router: Router, private loginService: LoginService,
    private startupService: StartupService, private _message: NzMessageService) {
    this.valForm = fb.group({
      username: [null, Validators.compose([Validators.required])],
      password: [null, Validators.required]
    });
  }

  submit() {
    for (const i in this.valForm.controls) {
      this.valForm.controls[i].markAsDirty();
    }
    if (this.valForm.valid) {
      console.log('Valid!');
      console.log(this.valForm.value);
      this.loginService.login(this.valForm.value.username, this.valForm.value.password)
          .subscribe(data => {
            if (data.success) {
              this.startupService.load();
      this.router.navigate(['dashboard']);
            }else {
              this._message.create('error', '用户名或密码不正确');
            }
          });
    }
  }
}



